.container {
    display: flex;
    width: 100%;
    box-shadow: 0px 2px 3px -1px black;
    border-radius: 5px;
    gap: 10px;
    padding: 10px;
    margin-block: 20px;
}

.name {
    font-size: 18px;
    font-weight: 500;
    color: var(--title-blue);
}

.header_text {
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.opacity {
    color: gray;
}